import moment from 'moment'

class timePicker {

    constructor({
        mode24hours = false,
        okText = "Ok",
        cancelText = "Cancel",
        currentTime = moment(),
    }) {
        this.mode24hours = mode24hours;
        this.controls = {};
        this.inner24hours = mode24hours;
        this.currentTime = currentTime;
        this.okText = okText;
        this.cancelText = cancelText;

        if (!document.getElementById('test-picker')) {
            this.buildComponent();
        }
    }

    set mode24Hours(value) {
        if (value === true) {
            this.mode24hours = true;
            this.inner24hours = true;
        } else {
            this.mode24hours = false;
            this.inner24hours = false;
        }
    }

    set setCurrentTime(value) {
        this.currentTime = moment(value, this.mode24hours ? "HH:mm" : "hh:mm A");
    }

    toggle(control) {
        if (control) {
            this.controlToSet = control;
            if (control.value) {
                this.currentTime = moment(control.value, this.mode24hours ? "HH:mm" : "hh:mm A");
            }
        }

        this.selectDialog();

        if (timePicker.dialog.state) {
            this.hideDialog()
        } else {
            this.show()
        }
    }

    selectDialog() {
        this.controls.picker = document.getElementById('test-picker');
        const controlsArray = [
            'header', 'cancel', 'ok', 'subtitle', 'AM', 'PM',
            'needle', 'hourView', 'minuteView', 'hour', 'minute',
            'fakeNeedle', 'circularHolder', 'circle', 'dotSpan'
        ];

        let i = controlsArray.length;
        while (i--) {
            this.controls[controlsArray[i]] = document.getElementById(`test-timeTime__${controlsArray[i]}`);
        }

        this.controls.ok.textContent = this.okText;
        this.controls.cancel.textContent = this.cancelText;
        this.controls.currentTime = this.currentTime.clone();
    }

    show() {
        this.selectDialog()
        this.initTimeDialog(this.currentTime);
        this.showDialog();
    }

    buildComponent() {
        const docFrag = document.createDocumentFragment();

        const container = document.createElement('div');
        container.id = 'test-picker';
        container.classList.add('test-picker');
        container.classList.add('test-picker');
        container.classList.add('test-picker--inactive');
        container.classList.add('animated');

        const header = document.createElement('div');
        this.addId(header, 'header');
        header.classList.add('test-picker__header');

        const body = document.createElement('div');
        this.addId(body, 'body');
        body.classList.add('test-picker__body');

        const action = document.createElement('div');
        const cancel = document.createElement('button');
        const ok = document.createElement('button');

        body.appendChild(action);
        container.appendChild(header);
        container.appendChild(body);

        // Circular view section (hours and minutes)
        const title = document.createElement('div');
        this.addId(title, 'title');
        title.classList.add('test-picker__title');

        const hour = document.createElement('span');
        this.addId(hour, 'hour');
        hour.classList.add('test-picker__color--active');

        const span = document.createElement('span');
        this.addId(span, 'dotSpan');
        span.style.display = 'none';
        span.textContent = ":";

        const minute = document.createElement('span');
        this.addId(minute, 'minute');

        const subtitle = document.createElement('div');
        subtitle.classList.add('test-picker__subtitle');
        this.addId(subtitle, 'subtitle');
        subtitle.style.display = 'none'

        const AM = document.createElement('div');
        this.addId(AM, 'AM');
        AM.textContent = moment().localeData().meridiem(1, 1, true).toUpperCase();

        const PM = document.createElement('div');
        this.addId(PM, 'PM');
        PM.textContent = moment().localeData().meridiem(13, 1, true).toUpperCase();


        title.appendChild(hour);
        title.appendChild(span);
        title.appendChild(minute);
        subtitle.appendChild(AM);
        subtitle.appendChild(PM);

        header.appendChild(title);
        header.appendChild(subtitle);

        const circularHolder = document.createElement('div');
        this.addId(circularHolder, 'circularHolder');
        circularHolder.classList.add('test-picker__circularHolder');

        const needle = document.createElement('div');
        this.addId(needle, 'needle');
        needle.classList.add('test-picker__selection');

        const dot = document.createElement('span');
        dot.classList.add('test-picker__dot');

        const line = document.createElement('span');
        line.classList.add('test-picker__line');

        const circle = document.createElement('span');
        this.addId(circle, 'circle');
        circle.classList.add('test-picker__circle');

        const minuteView = document.createElement('div');
        this.addId(minuteView, 'minuteView');
        minuteView.classList.add('test-picker__circularView');
        minuteView.classList.add('test-picker__circularView--hidden')

        const fakeNeedle = document.createElement('div');
        this.addId(fakeNeedle, 'fakeNeedle');
        fakeNeedle.classList.add('test-picker__circle--fake')

        const hourView = document.createElement('div');
        this.addId(hourView, 'hourView');
        hourView.classList.add('test-picker__circularView');

        needle.appendChild(dot);
        needle.appendChild(line);
        needle.appendChild(circle);

        circularHolder.appendChild(needle)
        circularHolder.appendChild(minuteView)
        circularHolder.appendChild(fakeNeedle)
        circularHolder.appendChild(hourView)

        body.appendChild(circularHolder)
        // end circular view section

        action.classList.add('test-picker__action');
        this.addId(cancel, 'cancel');
        cancel.classList.add('test-button');
        cancel.setAttribute('type', 'button');

        this.addId(ok, 'ok');
        ok.classList.add('test-button');
        ok.setAttribute('type', 'button');

        action.appendChild(cancel);
        action.appendChild(ok);
        body.appendChild(action);
        docFrag.appendChild(container);
        document.getElementsByTagName('body').item(0).appendChild(docFrag);
    }

    initTimeDialog(m) {
        const hour = this.controls.hour
        const minute = this.controls.minute
        const subtitle = this.controls.subtitle
        const dotSpan = this.controls.dotSpan

        if (this.mode24hours) {
            let text = parseInt(m.format('H'), 10)
            if (text === 0) {
                text = '00'
            }

            this.fillText(hour, text)
            dotSpan.style.display = 'initial'

        } else {
            this.fillText(hour, m.format('h'))

            if (m._locale.isPM(m.format('A'))) {
                this.controls.PM.classList.add('test-picker__color--active')
            } else {
                this.controls.AM.classList.add('test-picker__color--active')
            }
            subtitle.style.display = 'initial'
            dotSpan.style.display = 'initial'
        }

        this.fillText(minute, m.format('mm'))

        this.initHour();
        this.initMinute();
        this.attachEventHandlers();
        this.changeDayTime();
        this.dragDial();
        this.switchToView(hour, false);
        this.switchToView(minute, true);
        this.addClockEvent();
    }

    initHour() {
        const hourView = this.controls.hourView;
        const needle = this.controls.needle;
        const hour = 'test-hour__selected';
        const selected = 'test-picker__cell--selected';
        const rotate24 = 'test-picker__cell--rotate24';
        const cell = 'test-picker__cell';
        const docfrag = document.createDocumentFragment();
        let hourNow;
        if (this.mode24hours) {
            const degreeStep = (this.inner24hours === true) ? 10 : 5;
            hourNow = parseInt(this.controls.currentTime.format('H'), 10);

            for (let i = 1, j = degreeStep; i <= 24; i++ , j += degreeStep) {
                const div = document.createElement('div');
                const span = document.createElement('span');
                div.classList.add(cell);

                if (i === 24) {
                    span.textContent = '00';
                } else {
                    span.textContent = i;
                }

                let position = j;
                if (this.inner24hours === true && i > 12) {
                    position -= 120;
                    div.classList.add(rotate24);
                }

                //div.classList.add(rotate + position);
                const rotationNumber = this.calcRotationNumber(position);

                div.style.transform = `rotate(${rotationNumber}deg)`;
                span.style.transform = `rotate(${rotationNumber > 0 ? -Math.abs(rotationNumber) : Math.abs(rotationNumber)}deg)`;

                if (hourNow === i) {
                    div.id = hour;
                    div.classList.add(selected);
                    needle.style.transform = `rotate(${rotationNumber}deg)`;
                }

                if (i === 24 && hourNow === 0) {
                    div.id = hour;
                    div.classList.add(selected);
                    needle.style.transform = `rotate(${rotationNumber}deg)`;
                }

                div.appendChild(span);
                docfrag.appendChild(div);
            }
        } else {
            hourNow = parseInt(this.controls.currentTime.format('h'), 10);
            for (let i = 1, j = 10; i <= 12; i++ , j += 10) {
                const div = document.createElement('div');
                const span = document.createElement('span');
                const rotationNumber = this.calcRotationNumber(j);

                div.classList.add(cell);
                span.textContent = i;
                div.style.transform = `rotate(${rotationNumber}deg)`;
                span.style.transform = `rotate(${rotationNumber > 0 ? -Math.abs(rotationNumber) : Math.abs(rotationNumber)}deg)`;

                if (hourNow === i) {
                    div.id = hour;
                    div.classList.add(selected);
                    needle.style.transform = `rotate(${rotationNumber}deg)`;;
                }

                div.appendChild(span);
                docfrag.appendChild(div);
            }
        }

        while (hourView.lastChild) {
            hourView.removeChild(hourView.lastChild);
        }

        hourView.appendChild(docfrag);
    }

    initMinute() {
        const minuteView = this.controls.minuteView;
        let minuteNow = parseInt(this.controls.currentTime.format('m'), 10);
        const sMinute = 'test-minute__selected';
        const selected = 'test-picker__cell--selected';
        const cell = 'test-picker__cell';
        const docfrag = document.createDocumentFragment();

        for (let i = 5, j = 10; i <= 60; i += 5, j += 10) {
            const div = document.createElement('div');
            const span = document.createElement('span');
            const rotationNumber = this.calcRotationNumber(j);

            div.classList.add(cell);
            if (i === 60) {
                span.textContent = this.numWithZero(0);
            } else {
                span.textContent = this.numWithZero(i);
            }
            if (minuteNow === 0) {
                minuteNow = 60;
            }

            div.style.transform = `rotate(${rotationNumber}deg)`;
            span.style.transform = `rotate(${rotationNumber > 0 ? -Math.abs(rotationNumber) : Math.abs(rotationNumber)}deg)`;


            if ((minuteNow === i) || (minuteNow - 1 === i) || (minuteNow + 1 === i) || (minuteNow === 1 && i === 60)) {
                div.id = sMinute;
                div.classList.add(selected);
            }

            div.appendChild(span);
            docfrag.appendChild(div);
        }

        while (minuteView.lastChild) {
            minuteView.removeChild(minuteView.lastChild);
        }

        minuteView.appendChild(docfrag);
    }

    changeDayTime() {
        const me = this
        const AM = this.controls.AM
        const PM = this.controls.PM
        AM.onclick = function () {
            let d = 'AM'
            if (me.controls.currentTime._locale.isPM(me.controls.currentTime.format('A'))) {
                d = 'PM'
            }
            if (d === 'PM') {
                me.controls.currentTime.subtract(12, 'h')
                AM.classList.toggle('test-picker__color--active')
                PM.classList.toggle('test-picker__color--active')
            }
        }
        PM.onclick = function () {
            let d = 'AM'

            if (me.controls.currentTime._locale.isPM(me.controls.currentTime.format('A'))) {
                d = 'PM'
            }
            if (d === 'AM') {
                me.controls.currentTime.add(12, 'h')
                AM.classList.toggle('test-picker__color--active')
                PM.classList.toggle('test-picker__color--active')
            }
        }
    }

    switchToView(el, isMinuteView) {
        const me = this;

        if (this.inner24hours && me.mode24hours) {
            if (parseInt(this.controls.currentTime.format('H'), 10) > 12) {
                me.controls.needle.classList.add('test-picker__cell--rotate24');
            } else {
                me.controls.needle.classList.remove('test-picker__cell--rotate24');
            }
        }

        el.onclick = function () {
            if (el.classList.contains('test-picker__color--active') === false) {
                me.switchToTimeView(me, isMinuteView);
            }
        }
    }

    switchToTimeView(me, isMinuteView) {
        const hourView = me.controls.hourView;
        const minuteView = me.controls.minuteView;
        const hour = me.controls.hour;
        const minute = me.controls.minute;
        const activeClass = 'test-picker__color--active';
        const hidden = 'test-picker__circularView--hidden';

        hourView.classList.toggle(hidden);
        minuteView.classList.toggle(hidden);
        hour.classList.toggle(activeClass);
        minute.classList.toggle(activeClass);

        timePicker.dialog.view = !timePicker.dialog.view;

        me.pointNeedle(me, isMinuteView)
    }

    pointNeedle(me, isMinuteView) {
        let spoke = 60;
        let value;
        const circle = this.controls.circle;
        const fakeNeedle = this.controls.fakeNeedle;
        const circularHolder = this.controls.circularHolder;
        const selection = 'test-picker__selection';
        const needle = me.controls.needle;

        needle.className = '';
        needle.classList.add(selection);

        if (isMinuteView) {
            value = this.controls.currentTime.format('m');

            setTimeout(() => {
                const hOffset = circularHolder.getBoundingClientRect();
                const cOffset = circle.getBoundingClientRect();

                fakeNeedle.style.left = `${cOffset.left - hOffset.left + 10}px`;
                fakeNeedle.style.top = `${cOffset.top - hOffset.top + 10}px`;
            }, 300);
        } else if (me.mode24hours) {
            spoke = 24;
            value = parseInt(this.controls.currentTime.format('H'), 10);

            if (value === 0) {
                value = 24;
            }
        } else {
            spoke = 12;
            value = this.controls.currentTime.format('h');
        }

        const parsedValue = parseInt(value, 10);
        const rotationNumber = me.calcRotation(spoke, parsedValue);

        needle.style.transform = `rotate(${rotationNumber}deg)`;

        if (this.mode24hours === true && spoke === 24 && parsedValue > 12) {
            needle.classList.add('test-picker__cell--rotate24');
        }
    }

    calcRotation(spoke, value) {
        if (spoke === 12) {
            value *= 10;
        } else if (spoke === 24) {
            if (this.mode24hours === true) {
                if (value > 12) {
                    value -= 12;
                }
                value *= 10;
            } else {
                value *= 5;
            }
        } else {
            value *= 2;
        }
        if (spoke === 60 && value === 0) {
            value = 120;
        }

        return this.calcRotationNumber(value);
    }

    calcRotationNumber(val) {
        return val * 3 + -87 - 3;
    }

    attachEventHandlers() {
        const me = this;
        const ok = this.controls.ok;
        const cancel = this.controls.cancel;

        cancel.onclick = function () {
            me.toggle()
        }
        ok.onclick = function () {
            me.sendTime(me);
            me.toggle()
        }
    }

    sendTime(me) {
        if (me.controlToSet) {
            this.newTimeFormatted = me.controls.currentTime.format(me.mode24hours ? "HH:mm" : "hh:mm A");
            this.newTime = me.controls.currentTime;
            const onSetTime = new CustomEvent('onSetTime')
            me.controlToSet.dispatchEvent(onSetTime)
        }
    }

    addClockEvent() {
        const me = this;
        const hourView = this.controls.hourView;
        const minuteView = this.controls.minuteView;
        const sClass = 'test-picker__cell--selected';
        const needle = this.controls.needle;

        hourView.onclick = function (e) {
            const sHour = 'test-hour__selected';
            const selectedHour = document.getElementById(sHour);
            let setHour = 0;
            if (e.target && e.target.nodeName === 'SPAN') {
                selectedHour.id = '';
                selectedHour.classList.remove(sClass);
                e.target.parentNode.classList.add(sClass);
                e.target.parentNode.id = sHour;

                if (me.mode24hours) {
                    setHour = parseInt(e.target.textContent, 10);
                } else if (me.controls.currentTime.format('A') === 'AM') {
                    setHour = e.target.textContent;
                } else {
                    setHour = parseInt(e.target.textContent, 10) + 12;
                }

                me.controls.currentTime.hour(setHour);
                me.controls.hour.textContent = e.target.textContent;

                me.pointNeedle(me);
                setTimeout(() => {
                    me.switchToTimeView(me, true);
                }, 700)
            }
        }
        minuteView.onclick = function (e) {
            const sMinute = 'test-minute__selected';
            const selectedMinute = document.getElementById(sMinute);
            let setMinute = 0;
            if (e.target && e.target.nodeName === 'SPAN') {
                if (selectedMinute) {
                    selectedMinute.id = '';
                    selectedMinute.classList.remove(sClass);
                }

                e.target.parentNode.classList.add(sClass);
                e.target.parentNode.id = sMinute;

                setMinute = e.target.textContent;
                me.controls.currentTime.minute(setMinute);
                me.controls.minute.textContent = setMinute;
                me.pointNeedle(me, true);
            }
        }
    }

    showDialog() {
        const me = this
        const zoomIn = 'zoomIn'
        this.controls.picker.classList.remove('test-picker--inactive')
        this.controls.picker.classList.add(zoomIn)
        timePicker.dialog.state = true;

        setTimeout(() => {
            me.controls.picker.classList.remove(zoomIn)
        }, 300)
    }

    hideDialog() {
        this.selectDialog()

        const me = this;
        const title = me.controls.title;
        const subtitle = me.controls.subtitle;
        const AM = this.controls.AM;
        const PM = this.controls.PM;
        const minute = this.controls.minute;
        const hour = this.controls.hour;
        const minuteView = this.controls.minuteView;
        const hourView = this.controls.hourView;
        const needle = this.controls.needle;
        const dotSpan = this.controls.dotSpan;
        const active = 'test-picker__color--active';
        const inactive = 'test-picker--inactive';
        const invisible = 'test-picker__years--invisible';
        const zoomIn = 'zoomIn';
        const zoomOut = 'zoomOut';
        const hidden = 'test-picker__circularView--hidden';
        const selection = 'test-picker__selection';

        this.controls.picker.classList.add(zoomOut);
        timePicker.dialog.state = false;
        timePicker.dialog.view = true;

        AM.classList.remove(active);
        PM.classList.remove(active);
        minute.classList.remove(active);
        hour.classList.add(active);
        minuteView.classList.add(hidden);
        hourView.classList.remove(hidden);
        subtitle.style.display = 'none';
        dotSpan.style.display = 'none';
        needle.className = selection;

        setTimeout(() => {
            me.controls.picker.classList.remove('test-picker--portrait');
            me.controls.picker.classList.remove(zoomOut);
            me.controls.picker.classList.add(inactive);
        }, 300)
    }

    dragDial() {
        const me = this;
        const needle = this.controls.needle;
        const circle = this.controls.circle;
        const fakeNeedle = this.controls.fakeNeedle;
        const circularHolder = this.controls.circularHolder;
        const minute = this.controls.minute;
        const quick = 'test-picker__selection--quick';
        const selection = 'test-picker__selection';
        const selected = 'test-picker__cell--selected';
        let hOffset = circularHolder.getBoundingClientRect();
        let divides;
        const fakeNeedleDraggabilly = new Draggabilly(fakeNeedle, {
            containment: true
        })
        fakeNeedleDraggabilly.on('pointerDown', () => {
            hOffset = circularHolder.getBoundingClientRect()
        })

        fakeNeedleDraggabilly.on('pointerMove', (e) => {
            let clientX = e.clientX;
            let clientY = e.clientY;

            if (clientX === undefined) {
                if (e.pageX === undefined) {
                    if (e.touches && e.touches.length > 0) {
                        clientX = e.touches[0].clientX;
                        clientY = e.touches[0].clientY;
                    }
                } else {
                    clientX = e.pageX - document.body.scrollLeft - document.documentElement.scrollLeft;
                    clientY = e.pageY - document.body.scrollTop - document.documentElement.scrollTop;
                }
            }

            const xPos = clientX - hOffset.left - (hOffset.width / 2);
            const yPos = clientY - hOffset.top - (hOffset.height / 2);

            let slope = Math.atan2(-yPos, xPos);
            needle.className = '';
            if (slope < 0) {
                slope += 2 * Math.PI;
            }
            slope *= 180 / Math.PI;
            slope = 360 - slope;
            if (slope > 270) {
                slope -= 360;
            }
            divides = parseInt(slope / 6);

            const same = Math.abs((6 * divides) - slope);
            const upper = Math.abs((6 * (divides + 1)) - slope);

            if (upper < same) {
                divides++;
            }

            divides += 15;

            const rotationNumber = me.calcRotationNumber(divides * 2);;

            needle.classList.add(selection);
            needle.classList.add(quick);
            needle.style.transform = `rotate(${rotationNumber}deg)`;;

            minute.textContent = me.numWithZero(divides);
        })

        const onDragEnd = function () {
            const minuteViewChildren = me.controls.minuteView.getElementsByTagName('div');
            const sMinute = 'test-minute__selected';
            const selectedMinute = document.getElementById(sMinute);
            const cOffset = circle.getBoundingClientRect();

            fakeNeedle.style.left = `${cOffset.left - hOffset.left + 10}px`;
            fakeNeedle.style.top = `${cOffset.top - hOffset.top + 10}px`;
            needle.classList.remove(quick);
            let select = divides;

            if (select === 1) {
                select = 60;
            }
            select = me.nearestDivisor(select, 5);

            if (divides === 60) {
                divides = 0;
            }

            if (selectedMinute) {
                selectedMinute.id = '';
                selectedMinute.classList.remove(selected);
            }

            if (select > 0) {
                select /= 5;
                select--;
                minuteViewChildren[select].id = sMinute;
                minuteViewChildren[select].classList.add(selected);
            }

            minute.textContent = me.numWithZero(divides);
            me.controls.currentTime.minutes(divides);
        }

        fakeNeedleDraggabilly.on('pointerUp', onDragEnd);
        fakeNeedleDraggabilly.on('dragEnd', onDragEnd);
    }

    addId(el, id) {
        el.id = `test-timeTime__${id}`;
    }

    fillText(el, text) {
        if (el.firstChild) {
            el.firstChild.nodeValue = text
        } else {
            el.appendChild(document.createTextNode(text))
        }
    }

    numWithZero(n) {
        return n > 9 ? `${n}` : `0${n}`
    }

    nearestDivisor(number, divided) {
        if (number % divided === 0) {
            return number
        } else if ((number - 1) % divided === 0) {
            return number - 1
        } else if ((number + 1) % divided === 0) {
            return number + 1
        }
        return -1
    }
}

timePicker.dialog = {
    state: false,
    view: true
}

export default timePicker


class TimePickerControl extends HTMLElement {
    constructor() {
        super();

        this._mode24hours = false;
        this._oktext = "Ok";
        this._canceltext = "Cancel";
        this._placeholder = "";
        this._cssclass = "";
        this._time = "";
        this.style.width = "100%";

        this.dialog = new timePicker({
            mode24Hours: this._mode24hours,
            okText: this._oktext,
            cancelText: this._canceltext
        });
    }

    get time() {
        return this._time;
    }
    set time(value) {
        this._time = value;
        this.setAttribute('time', value);
    }

    get mode24Hours() {
        return this._mode24hours
    }
    set mode24hours(value) {
        this._mode24hours = value;
        this.setAttribute('mode24Hours', value);
    }

    get oktext() {
        return this._oktext
    }
    set oktext(value) {
        this._oktext = value;
        this.setAttribute('okText', value);
    }

    get canceltext() {
        return this._canceltext
    }
    set canceltext(value) {
        this._canceltext = value;
        this.setAttribute('cancelText', value);
    }

    get placeholder() {
        return this._placeholder
    }
    set placeholder(value) {
        this._placeholder = value;
        this.setAttribute('placeHolder', value);
    }

    get cssclass() {
        return this._cssclass
    }
    set cssclass(value) {
        this._cssclass = value;
        this.setAttribute('class', value);
    }

    static get observedAttributes() {
        return ['mode24hours', 'oktext', 'canceltext', 'placeholder', 'cssclass', 'time'];
    }

    attributeChangedCallback(name, oldVal, newVal) {
        switch (name) {
            case 'mode24hours':
                this._mode24hours = newVal;
                this.dialog.mode24Hours = newVal === "true";
                break;
            case 'oktext':
                this._oktext = newVal;
                this.dialog.okText = newVal;
                break;
            case 'canceltext':
                this._canceltext = newVal;
                this.dialog.cancelText = newVal;
                break
            case 'placeholder':
                this._placeholder = newVal;
                break;
            case 'cssclass':
                this._cssclass = newVal;
                break;
            case 'time':
                this.dialog.setCurrentTime = newVal;
                break;
        }
    }

    connectedCallback() {
        const template = `<input type="text"
             placeholder="${this._placeholder}" class="${this._cssclass}" readonly /> `

        this.innerHTML = template;

        this.addEventListener("click", e => {
            this.dialog.toggle(this)
        });

        this.addEventListener('onSetTime', function () {
            this.querySelector('input').value = this.dialog.newTimeFormatted;
            this.time = this.dialog.newTimeFormatted;
        });
    }
}

window.customElements.define('time-picker', TimePickerControl);